const ddr5Tariffs = tariffs.filter(t => t.ramType === "DDR5")
const ddr4Tariffs = tariffs.filter(t => t.ramType === "DDR4")

export default function BuyingPage() {
  const [selectedTariff, setSelectedTariff] = useState<string | null>(null)
  const [paymentMethod, setPaymentMethod] = useState<PaymentMethod>("balance")
  const [serverName, setServerName] = useState("")
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)
  const { t } = useLanguage()
  const { formatPrice, convertPrice } = useCurrency()
  const [searchQuery, setSearchQuery] = useState("")

  const handleBuy = async () => {
    if (!selectedTariff || !serverName) return

    setLoading(true)
    setSuccess(false)
    const tariff = tariffs.find(t => t.id === selectedTariff)
    if (!tariff) return

    try {
      const response = await fetch("/api/orders", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          serverName,
          serverPlan: tariff.name,
          billingPeriod: "month",
          price: convertPrice(tariff.prices.month),
          paymentMethod,
        }),
      })

      const data = await response.json()
      if (data.paymentUrl) {
        window.location.href = data.paymentUrl
      } else {
        setSuccess(true)
        setTimeout(() => setSuccess(false), 4000)
      }
    } finally {
      setLoading(false)
    }
  }

  const filterTariffs = (tariffsList) => {
    return tariffsList.filter(tariff => 
      tariff.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      tariff.id.toLowerCase().includes(searchQuery.toLowerCase())
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-muted/50">
      <Header />
      <div className="max-w-6xl mx-auto py-8 px-4 sm:py-12 sm:px-6 lg:px-8">
        <motion.div initial={{ opacity: 0, y: -20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.5 }}>
          <Button variant="outline" className="mb-4 sm:mb-6 border-primary/20 hover:bg-primary/5" type="button" onClick={() => window.history.back()}>
            ← {t("common.back") || "Back"}
          </Button>
        </motion.div>
        <motion.h1 
          className="text-2xl sm:text-3xl font-bold text-foreground mb-6 sm:mb-8 bg-clip-text text-transparent bg-gradient-to-r from-primary to-primary/50"
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5 }}
        >
          {t("servers.buyTitle") || "Buy Server"}
        </motion.h1>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 sm:gap-8">
          {/* Tariff List */}
          <motion.div 
            className="order-2 md:order-1"
            initial={{ opacity: 0, x: -50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            <h2 className="text-xl sm:text-2xl font-semibold mb-3 sm:mb-4">{t("servers.choosePlan") || "Choose Plan"}</h2>
            <Input
              placeholder={t("servers.searchPlaceholder") || "Search tariffs..."}
              value={searchQuery}
              onChange={e => setSearchQuery(e.target.value)}
              className="mb-4 bg-background/50 border-primary/20 placeholder-muted-foreground/50"
            />
            <Tabs defaultValue="ddr5" className="relative mr-auto w-full">
              <div className="flex items-center justify-between pb-3">
                <TabsList className="w-full justify-start rounded-md bg-muted p-1 text-muted-foreground md:w-auto">
                  <TabsTrigger value="ddr5" className="flex-1 md:flex-none">
                    DDR5
                  </TabsTrigger>
                  <TabsTrigger value="ddr4" className="flex-1 md:flex-none">
                    DDR4
                  </TabsTrigger>
                </TabsList>
              </div>
              <ScrollArea className="h-[calc(100vh-400px)] md:h-[calc(100vh-300px)] pr-2">
                <TabsContent value="ddr5">
                  <AnimatePresence>
                    {filterTariffs(ddr5Tariffs).map((tariff, index) => (
                      <Tooltip key={tariff.id} delayDuration={0}>
                        <TooltipTrigger asChild>
                          <motion.div
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -20 }}
                            transition={{ duration: 0.3, delay: index * 0.05 }}
                            className={`mb-3 transition-all duration-300 cursor-pointer border rounded-lg sm:rounded-xl p-4 sm:p-5 bg-card/80 backdrop-blur-sm flex flex-col sm:flex-row items-start sm:items-center gap-3 sm:gap-4 shadow-md hover:shadow-lg hover:scale-105 ${
                              selectedTariff === tariff.id ? "border-primary bg-primary/5 scale-105" : "border-border/50 hover:border-primary/30"
                            }`}
                            onClick={() => setSelectedTariff(tariff.id)}
                          >
                            <div className="w-10 h-10 sm:w-12 sm:h-12 rounded-md sm:rounded-lg flex items-center justify-center bg-primary/10 flex-shrink-0">
                              <Server className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                            </div>
                            <div className="flex-1 w-full">
                              <div className="font-semibold text-base sm:text-lg text-foreground">{tariff.name}</div>
                              <div className="text-xs sm:text-sm text-muted-foreground mt-1">
                                {tariff.cores} CPU &middot; {tariff.ram} GB {tariff.ramType} &middot; {tariff.storage} GB {tariff.storageType}
                              </div>
                            </div>
                            <div className="flex flex-row sm:flex-col items-end gap-2 sm:gap-1 min-w-[120px] sm:min-w-[140px] w-full sm:w-auto justify-between sm:justify-start">
                              <div className="text-base sm:text-xl font-bold text-primary">
                                {formatPrice(tariff.prices.month)} <span className="text-xs sm:text-sm text-muted-foreground">/{t("servers.perMonth") || "mo"}</span>
                              </div>
                            </div>
                          </motion.div>
                        </TooltipTrigger>
                        <TooltipContent side="top" className="max-w-xs">
                          <p>{t("servers.tariffTooltip") || "Click to select this plan for your server"}</p>
                        </TooltipContent>
                      </Tooltip>
                    ))}
                  </AnimatePresence>
                </TabsContent>
                <TabsContent value="ddr4">
                  <AnimatePresence>
                    {filterTariffs(ddr4Tariffs).map((tariff, index) => (
                      <Tooltip key={tariff.id} delayDuration={0}>
                        <TooltipTrigger asChild>
                          <motion.div
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -20 }}
                            transition={{ duration: 0.3, delay: index * 0.05 }}
                            className={`mb-3 transition-all duration-300 cursor-pointer border rounded-lg sm:rounded-xl p-4 sm:p-5 bg-card/80 backdrop-blur-sm flex flex-col sm:flex-row items-start sm:items-center gap-3 sm:gap-4 shadow-md hover:shadow-lg hover:scale-105 ${
                              selectedTariff === tariff.id ? "border-primary bg-primary/5 scale-105" : "border-border/50 hover:border-primary/30"
                            }`}
                            onClick={() => setSelectedTariff(tariff.id)}
                          >
                            <div className="w-10 h-10 sm:w-12 sm:h-12 rounded-md sm:rounded-lg flex items-center justify-center bg-primary/10 flex-shrink-0">
                              <Server className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                            </div>
                            <div className="flex-1 w-full">
                              <div className="font-semibold text-base sm:text-lg text-foreground">{tariff.name}</div>
                              <div className="text-xs sm:text-sm text-muted-foreground mt-1">
                                {tariff.cores} CPU &middot; {tariff.ram} GB {tariff.ramType} &middot; {tariff.storage} GB {tariff.storageType}
                              </div>
                            </div>
                            <div className="flex flex-row sm:flex-col items-end gap-2 sm:gap-1 min-w-[120px] sm:min-w-[140px] w-full sm:w-auto justify-between sm:justify-start">
                              <div className="text-base sm:text-xl font-bold text-primary">
                                {formatPrice(tariff.prices.month)} <span className="text-xs sm:text-sm text-muted-foreground">/{t("servers.perMonth") || "mo"}</span>
                              </div>
                            </div>
                          </motion.div>
                        </TooltipTrigger>
                        <TooltipContent side="top" className="max-w-xs">
                          <p>{t("servers.tariffTooltip") || "Click to select this plan for your server"}</p>
                        </TooltipContent>
                      </Tooltip>
                    ))}
                  </AnimatePresence>
                </TabsContent>
              </ScrollArea>
            </Tabs>
          </motion.div>
          {/* Order Panel */}
          <motion.div 
            className="order-1 md:order-2 space-y-4 sm:space-y-6 md:sticky md:top-20 self-start"
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            <h2 className="text-xl sm:text-2xl font-semibold">{t("servers.orderConfig") || "Order Configuration"}</h2>
            <motion.div 
              className="flex flex-col gap-4 sm:gap-5 bg-card/80 backdrop-blur-md p-4 sm:p-6 rounded-lg sm:rounded-xl shadow-lg border border-primary/10"
              initial={{ opacity: 0, scale: 0.95 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.3 }}
            >
              <div>
                <label className="block mb-1 sm:mb-2 font-medium text-foreground">{t("servers.nameLabel") || "Server Name"}</label>
                <Input
                  placeholder={t("servers.namePlaceholder") || "e.g., my-server"}
                  value={serverName}
                  onChange={e => setServerName(e.target.value)}
                  disabled={loading}
                  className="w-full text-sm sm:text-base bg-background/50 border-primary/20"
                />
              </div>
              <div>
                <label className="block mb-1 sm:mb-2 font-medium text-foreground">{t("servers.billing") || "Billing Period"}</label>
                <div className="flex gap-2 sm:gap-3">
                  <Button
                    variant="default"
                    className="flex-1 text-sm sm:text-base py-2 sm:py-0 transition-all duration-300 hover:scale-105"
                    disabled
                  >
                    {t("servers.monthly") || "Monthly"}
                  </Button>
                </div>
              </div>
              <div>
                <label className="block mb-1 sm:mb-2 font-medium text-foreground">{t("payment.method") || "Payment Method"}</label>
                <div className="flex gap-2 sm:gap-3 flex-wrap">
                  <Button
                    variant={paymentMethod === "balance" ? "default" : "outline"}
                    onClick={() => setPaymentMethod("balance")}
                    className="flex-1 min-w-[100px] text-sm sm:text-base py-2 sm:py-0 transition-all duration-300 hover:scale-105"
                    disabled={loading}
                  >
                    {t("payment.balance") || "Balance"}
                  </Button>
                  <Button
                    variant={paymentMethod === "crystalpay" ? "default" : "outline"}
                    onClick={() => setPaymentMethod("crystalpay")}
                    className="flex-1 min-w-[100px] text-sm sm:text-base py-2 sm:py-0 transition-all duration-300 hover:scale-105"
                    disabled={loading}
                  >
                    {t("payment.crystalpay") || "CrystalPay"}
                  </Button>
                  <Button
                    variant={paymentMethod === "lzt" ? "default" : "outline"}
                    onClick={() => setPaymentMethod("lzt")}
                    className="flex-1 min-w-[100px] text-sm sm:text-base py-2 sm:py-0 transition-all duration-300 hover:scale-105"
                    disabled={loading}
                  >
                    {t("payment.lzt") || "LZT"}
                  </Button>
                </div>
              </div>
              {selectedTariff && (
                <motion.div
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3 }}
                >
                  <p className="font-medium mb-1 sm:mb-2 text-foreground">{t("servers.selectedPlan") || "Selected Plan:"}</p>
                  <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between bg-background/50 p-3 rounded-lg border border-primary/20 gap-2 sm:gap-0">
                    <Badge variant="secondary" className="text-xs sm:text-sm bg-primary/10 text-primary">
                      {tariffs.find(t => t.id === selectedTariff)?.name}
                    </Badge>
                    <span className="font-semibold text-primary text-sm sm:text-base">
                      {formatPrice(tariffs.find(t => t.id === selectedTariff)?.prices.month || 0)}
                      <span className="text-muted-foreground ml-1">
                        /{t("servers.perMonth") || "mo"}
                      </span>
                    </span>
                  </div>
                </motion.div>
              )}
              <Button
                className="w-full mt-2 sm:mt-4 text-sm sm:text-base transition-all duration-300 hover:scale-105 disabled:scale-100"
                onClick={handleBuy}
                disabled={!selectedTariff || !serverName || loading}
                size="lg"
              >
                {loading
                  ? t("servers.buying") || "Purchasing..."
                  : t("servers.buyButton") || "Purchase"}
              </Button>
              <AnimatePresence>
                {success && (
                  <motion.div
                    initial={{ opacity: 0, y: 10, scale: 0.95 }}
                    animate={{ opacity: 1, y: 0, scale: 1 }}
                    exit={{ opacity: 0, y: -10, scale: 0.95 }}
                    className="text-green-500 font-medium text-center mt-2 sm:mt-3 text-sm sm:text-base flex items-center justify-center gap-2"
                  >
                    <span className="animate-pulse">🎉</span> {t("servers.successOrder") || "Server successfully ordered!"}
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.div>
          </motion.div>
        </div>
      </div>
    </div>
  )
}